import React, { useState, useEffect } from 'react'
import { Section } from './styled'
import { Input, Button } from '../components'
import { management } from '../platform'
import { DEFAULT_SETTINGS } from '../settings'

const DevTools = ({ settings, onChange }) => {
  const [isDeveloperMode, setDeveloperMode] = useState(false)

  const onCloudAppUrlChanged = (ev) => {
    onChange({
      ...settings,
      cloudAppUrl: ev.target.value,
    })
  }

  const onResetCloudAppUrl = () => {
    onChange({
      ...settings,
      cloudAppUrl: DEFAULT_SETTINGS.cloudAppUrl,
    })
  }

  useEffect(() => {
    management.getSelf((info) => {
      setDeveloperMode(info.installType === 'development')
    })
  }, [])

  if (!isDeveloperMode) {
    return null
  }

  return (
    <Section header="Developer Tools">
      <Input
        label="Cloud App URL"
        value={settings.cloudAppUrl}
        buttons={[<Button key="reset" label="Reset to default" onClick={onResetCloudAppUrl} />]}
        onChange={onCloudAppUrlChanged}
      />
    </Section>
  )
}

export { DevTools }
