import React, { useEffect, useState, useCallback } from 'react'
import ReactDOM from 'react-dom'
import styled from 'styled-components'
import { formatDistance } from 'date-fns/esm'

import { storage } from '../platform'

import { BackgroundClient } from '../background/messaging/client'
import { GlobalStyles } from '../popup/styled'
import { DownloadIcon, OpenInCloudIcon, Section } from './styled'
import { ToggleSwitch } from '../components'
import { DevTools } from './DevTools'
import { DEFAULT_SETTINGS, OPEN_IN_EXISTING_TAB, OPEN_IN_NEW_TAB } from '../settings'
import { useDebounce } from './hooks'

const client = new BackgroundClient({ name: 'options-client' })

const getItemDetails = ({ pages = [], entries = [] }) => {
  if (pages[0]) {
    return {
      title: pages[0].title,
      started: pages[0].startedDateTime,
    }
  }

  return {
    title: entries[0].request.url,
    started: entries[0].startedDateTime,
  }
}

const HistoryEntry = ({ id, entry, settings }) => {
  const item = getItemDetails(entry.log)

  const download = () => {
    client.download(id)
  }

  const open = () => {
    client.openInCloud(id, settings.reuseExistingTab ? OPEN_IN_EXISTING_TAB : OPEN_IN_NEW_TAB)
  }

  return (
    <li key={item.started}>
      <div className="title" title={item.title}>
        <strong>{item.title}</strong>
      </div>
      <div className="time">({formatDistance(Date.parse(item.started), Date.now())} ago)</div>
      <div className="actions">
        <button className="btn" onClick={download}>
          <div>
            <DownloadIcon />
          </div>
          <div>Download</div>
        </button>
        <button className="btn primary" onClick={open}>
          <div>
            <OpenInCloudIcon />
          </div>
          <div>Open in k6.io</div>
        </button>
      </div>
    </li>
  )
}

const List = styled.ul`
  width: 100%;
  max-width: 768px;

  padding: 0;
  margin: 0;

  li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding: 10px 0;
    border-top: 1px solid #d4d4d4;
  }

  li:first-child {
    border-top: none;
  }

  .title {
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .actions {
    margin-left: 5px;
    display: flex;
  }

  .actions > button {
    margin-left: 5px;
  }

  .btn {
    display: flex;
    align-items: center;
  }

  .btn .icon {
    display: block;
    margin-right: 5px;
  }

  .btn.primary {
    background-color: #7d64ff;
    color: #fff;
    &:hover {
      background-color: #6450cc;
    }
  }
`

const RecordingHistory = ({ settings }) => {
  const [history, setHistory] = useState([])

  const loadHistory = async () => {
    const history = await client.listRecordings()

    setHistory(history)
  }

  useEffect(() => {
    loadHistory()
  }, [])

  return (
    <Section header="Recording history">
      {history.length == 0 ? (
        <div>You haven&apos;t recorded anything yet...</div>
      ) : (
        <List>
          {history.map((entry, index) => (
            <HistoryEntry key={index} id={index} entry={entry} settings={settings} />
          ))}
        </List>
      )}
    </Section>
  )
}

const Settings = ({ settings, onChange }) => {
  const toggleReuseExistingTab = () => {
    onChange({
      ...settings,
      reuseExistingTab: !settings.reuseExistingTab,
    })
  }

  return (
    <>
      <Section header="Settings">
        <div className="options">
          <ToggleSwitch
            label="Reuse existing tab on open"
            isOn={settings.reuseExistingTab}
            onClick={toggleReuseExistingTab}
          />
        </div>
      </Section>
      <DevTools settings={settings} onChange={onChange} />
    </>
  )
}

const Body = styled.main`
  max-width: 768px;
  margin-left: auto;
  margin-right: auto;
`

const App = () => {
  const [settings, setSettings] = useState(null)

  const onSettingsChanged = (settings) => {
    setSettings(settings)
  }

  const onSettingsCommitted = useCallback((newSettings) => {
    console.log('committed settings', newSettings)

    storage.local.set(newSettings, () => setSettings(newSettings))
  }, [])

  useDebounce(settings, onSettingsCommitted, 2000)

  useEffect(() => {
    storage.local.get(DEFAULT_SETTINGS, (settings) => setSettings(settings))
  }, [])

  return (
    <>
      <GlobalStyles />
      <div className="layout" style={{ width: '100%' }}>
        <div className="layout__header">
          <div className="logo">
            <svg width="40px" height="40px" viewBox="0 0 40 40">
              <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                <g fill="#7D64FF" fillRule="nonzero">
                  <path
                    d="M40,39.0697674 L0,39.0697674 L13.3154654,10.4762471 L21.3371967,16.411329 L31.8000724,0 L40,39.0697674 Z M25.3342992,32.8919165 L25.4204998,32.8919165 C26.4105301,32.8939273 27.3626447,32.5089059 28.0760594,31.8180465 C28.816664,31.1432125 29.2335132,30.1806802 29.2205723,29.1752911 C29.2536728,28.2059062 28.8491702,27.2735041 28.1202463,26.6389749 C27.5127027,26.025549 26.6937105,25.6712496 25.8333937,25.649673 L25.7681999,25.649673 C25.6559814,25.6494354 25.5442913,25.6651432 25.4364361,25.6963313 L27.5487142,22.5381475 L25.866715,21.3556511 L25.0699022,22.5381475 L23.0351322,25.6657118 C22.685259,26.1840564 22.3926114,26.641162 22.2122419,26.9531894 C22.0241902,27.2889838 21.8624068,27.6390072 21.7283593,28.0000851 C21.5757843,28.3835873 21.4971183,28.7928085 21.4965592,29.2059106 C21.4871219,30.1983202 21.8968286,31.1480867 22.6236871,31.8187755 C23.3278092,32.5090247 24.2724098,32.8944773 25.2553423,32.8926455 L25.3342992,32.8919165 Z M16.3911626,29.4909636 L18.7222021,32.8088064 L21.2140529,32.8088064 L18.4715683,28.9587674 L20.9083665,25.5541693 L19.2922854,24.4278087 L18.5737052,25.3755555 L16.3882651,28.4885392 L16.3882651,22.2414298 L14.2057226,20.4501884 L14.2057226,32.8102645 L16.3882651,32.8102645 L16.3882651,29.4909636 L16.3911626,29.4909636 Z M25.3371967,30.8192672 C24.4506606,30.8192672 23.7319812,30.0959636 23.7319812,29.2037235 C23.7319812,28.3114833 24.4506606,27.5881797 25.3371967,27.5881797 L25.3516842,27.5881797 C25.7786951,27.589121 26.1869324,27.7649424 26.4824339,28.0751758 C26.7936543,28.3628931 26.9694312,28.7696751 26.9663166,29.194975 C26.9571873,30.0941845 26.2307038,30.8185152 25.3371967,30.8192672 Z"
                    id="Exclusion_1"
                  ></path>
                </g>
              </g>
            </svg>
          </div>
        </div>
        <div className="layout__body">
          {settings && (
            <Body>
              <RecordingHistory settings={settings} />
              <Settings settings={settings} onChange={onSettingsChanged} />
            </Body>
          )}
        </div>
      </div>
    </>
  )
}

ReactDOM.render(<App />, document.getElementById('root'))
